import { AppBar, Container, Toolbar, Typography, Box, IconButton, Menu, MenuItem, Button, Grid } from "@mui/material";
import MenuIcon from '@mui/icons-material/Menu';
import ArrowDropDownIcon from '@mui/icons-material/ArrowDropDown';
import React, { useState, useEffect} from "react";
import { useNavigate } from 'react-router-dom';

const pages = ['Home','Products', 'Contact','Join Us', 'About'];
const services = [
  { heading: 'Website Development', items: ['Service 1A', 'Service 1B', 'Service 1C', 'Service 1D'] },
  { heading: 'Mobile App Development', items: ['Service 2A', 'Service 2B', 'Service 2C', 'Service 2D'] },
  { heading: 'API Services', items: ['Service 1A', 'Service 1B', 'Service 1C', 'Service 1D'] },
  { heading: 'Data Management', items: ['Service 2A', 'Service 2B', 'Service 2C', 'Service 2D'] }
]; // List of services with headings and items

export default function Header(props) {
  const [scrolling, setScrolling] = useState(false);
  const [anchorElNav, setAnchorElNav] = useState(null);
  const [anchorElServices, setAnchorElServices] = useState(null);
  const navigate = useNavigate();

  const handleOpenNavMenu = (event) => {
    setAnchorElNav(event.currentTarget);
  };

  const handleCloseNavMenu = (event) => {
    setAnchorElNav(null);
    const Page = event.currentTarget.textContent;
    if (Page === "Products") {
      navigate('/product');
    } else if (Page === "Contact") {
      navigate('/contact');
    } else if (Page === "About") {
      navigate('/about');
    }else if(Page==="Home"){
      navigate('/');
    }else if(Page==="Join Us"){
      navigate('/join');
    }
  };

  const handleOpenServicesMenu = (event) => {
    setAnchorElServices(event.currentTarget);
  };

  const handleCloseServicesMenu = (event) => {
    setAnchorElServices(null);
    const Service = event.currentTarget.textContent;
    console.log(`Navigate to ${Service}`);
  };

  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 100) {
        setScrolling(true);
      } else {
        setScrolling(false);
      }
    };

    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  return (
    <AppBar
      position="fixed"
      elevation={0}
      style={{ backgroundColor: scrolling ? "rgb(51, 72, 94)" : "transparent" }}
    >
      <Container maxWidth="xl">
        <Toolbar>
          <Typography variant="h5">Tekshu Innovations</Typography>
          <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' }, marginLeft: '100px' }}>
            {pages.map((page) => (
              <Button
                key={page}
                onClick={handleCloseNavMenu}
                sx={{ my: 2, color: 'white', display: 'block' ,marginLeft:'30px'}}
              >
                {page}
              </Button>
            ))}
            <Button
              onClick={handleOpenServicesMenu}
              sx={{ my: 2, color: 'white', display: 'flex', alignItems: 'center',marginLeft:'30px' }}
            >
              Services <ArrowDropDownIcon />
            </Button>
            <Menu
              anchorEl={anchorElServices}
              anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'left',
              }}
              open={Boolean(anchorElServices)}
              onClose={handleCloseServicesMenu}
            >
              <Grid container sx={{ p:2 }} maxWidth={700}>
                {services.map((service) => (
                  <Grid item xs={6} key={service.heading} sx={{ mb:2}}>
                    <Typography variant="h6" gutterBottom>
                      {service.heading}
                    </Typography>
                    <Grid container spacing={2}>
                      {service.items.map((item, index) => (
                        <Grid item xs={12} key={index}>
                          <Typography>{item}</Typography>
                        </Grid>
                      ))}
                    </Grid>
                  </Grid>
                ))}
              </Grid>
            </Menu>
          </Box>

          
          <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' }, justifyContent: 'flex-end' }}>
            <IconButton
              size="large"
              aria-label="top nav"
              aria-controls="menu-appbar"
              aria-haspopup="true"
              onClick={handleOpenNavMenu}
              color="inherit"
            >
              <MenuIcon />
            </IconButton>
            <Menu
              id="menu-appbar"
              anchorEl={anchorElNav}
              anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'left',
              }}
              open={Boolean(anchorElNav)}
              onClose={handleCloseNavMenu}
              sx={{
                display: { xs: 'block', md: 'none' },
              }}
            >
              {pages.map((page) => (
                <MenuItem key={page} onClick={handleCloseNavMenu}>
                  <Typography textAlign="center">{page}</Typography>
                </MenuItem>
              ))}
              <MenuItem onClick={handleOpenServicesMenu}>
                <Typography textAlign="center">Services <ArrowDropDownIcon /></Typography>
              </MenuItem>
            </Menu>
            <Menu
              anchorEl={anchorElServices}
              anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'left',
              }}
              open={Boolean(anchorElServices)}
              onClose={handleCloseServicesMenu}
              sx={{
                display: { xs: 'block', md: 'none' },
              }}
            >
              <Box sx={{ p: 2 }}>
                {services.map((service) => (
                  <Box key={service.heading} sx={{ mb: 2 }}>
                    <Typography variant="h6" gutterBottom>
                      {service.heading}
                    </Typography>
                    <Grid container spacing={2}>
                      {service.items.map((item, index) => (
                        <Grid item xs={6} key={index}>
                          <Typography>{item}</Typography>
                        </Grid>
                      ))}
                    </Grid>
                  </Box>
                ))}
              </Box>
            </Menu>
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
}
