import React from "react";
import "./style.css";
import Slider from "react-slick";
import {
  Card,
  CardContent,
  CardMedia,
  Typography,
  Box,
  Rating,
  Button,
} from "@mui/material";
import { useNavigate } from "react-router-dom";

const ProductCarousel = ({ products, productBuy, open }) => {
  const navigate = useNavigate();
  const handleKnowMore = () => {
    navigate("/product");
  };

  const handleProductBuy = (index) => {
    open(true);
    productBuy(index);
  };

  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 2,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };

  return (
    <Slider {...settings} justifyContent="center">
      {products.map((product, index) => (
        <ProductCard
          key={index}
          product={product}
          index={index}
          onClick={handleKnowMore}
          buy={handleProductBuy}
        />
      ))}
    </Slider>
  );
};

const ProductCard = ({ product, index, onClick, buy }) => (
  <Card
    sx={{ maxWidth: 345, margin: "0 0" }}
    elevation={1}
    style={{ backgroundColor: "rgb(51, 72, 94)" }}
  >
    <CardMedia
      component="img"
      height="140"
      image={product.image}
      alt={product.name}
    />
    <CardContent>
      <Typography gutterBottom variant="h5" component="div" color="white">
        {product.name}
      </Typography>
      <Typography variant="body2" color="white">
        {product.description}
      </Typography>
      <Box
        mt={2}
        display="flex"
        justifyContent="space-between"
        alignItems="center"
      >
        <Typography variant="h6" component="div" color="white">
          {product.price}
        </Typography>
        <Rating
          name="read-only"
          value={product.rating}
          readOnly
          sx={{
            "& .MuiRating-iconEmpty": {
              color: "white", // Customize the color here
            },
          }}
        />
      </Box>
      <Box display="flex" justifyContent="center" marginTop={3}>
        <Button variant="contained" onClick={() => buy(index)}>
          Buy
        </Button>
        <Button
          variant="contained"
          style={{ marginLeft: "10px" }}
          onClick={onClick}
        >
          Know More
        </Button>
      </Box>
    </CardContent>
  </Card>
);

export default ProductCarousel;
