import React, { useState } from "react";
import PaperModel from "../utils/PaperModel";
import { Box, Container } from "@mui/material";
import ProductCarousel from "../utils/ProductCarousel";
import PaymentDialog from "../utils/PaymentDialog";

export default function Dashboard() {
  const data1 = [
    {
      title: "Cutting Edge Solution",
      description:
        "Leveraging the latest technologies and innovative approaches, we provide solutions that are at the forefront of industry advancements, ensuring you stay ahead of the competition",
      image: "/backgrounds/tech_back.jpg",
    },
    {
      title: "High Quality",
      description:
        "Our commitment to excellence guarantees that every product and service we offer meets the highest standards of quality, delivering reliable and outstanding performance",
      image: "/backgrounds/tech_back.jpg",
    },
    {
      title: "Fast Service",
      description:
        "We prioritize efficiency and speed without compromising on quality, ensuring that your needs are met promptly and effectively, keeping your operations running smoothly",
      image: "/backgrounds/tech_back.jpg",
    },
  ];

  const products = [
    {
      name: "E-Learning App",
      description: "This is a short description of product 1.",
      price: 59999.00,
      rating: 4.5,
      image: "/backgrounds/app.jpg",
      otherInfo: "Additional information about product 1.",
    },
    {
      name: "Chatting App",
      description: "This is a short description of product 2.",
      price: 40000.00,
      rating: 4.0,
      image: "/backgrounds/app.jpg",
      otherInfo: "Additional information about product 2.",
    },
    {
      name: "Email Auth API",
      description: "This is a short description of product 1.",
      price: 99.00,
      rating: 2.0,
      image: "/backgrounds/app.jpg",
      otherInfo: "Additional information about product 1.",
    },
    {
      name: "Realtime Database API",
      description: "This is a short description of product 2.",
      price: 9999.00,
      rating: 1.5,
      image: "/backgrounds/app.jpg",
      otherInfo: "Additional information about product 2.",
    }
  ];

  const [open, setOpen] = useState(false);
  const [product, setProduct] = useState("");

  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const handleProductBuy = (value) => {
    console.log(value);
    setProduct(products[value]);
  };
  return (
    <div className="dashboard-container">
      <div className="dashboard-topview" />
      <div className="section1">
        <Box
          maxWidth="md"
          display="flex"
          flexDirection="column"
          justifyContent="center"
          alignItems="center"
          marginTop={5}
        >
          <h1>WHY US</h1>
          <p>
            At Tekshu Innovations, we pride ourselves on delivering top-notch
            technical solutions tailored to meet your unique needs. Our team of
            seasoned professionals has extensive experience in app development,
            website development, API integration, and technical consulting,
            ensuring that we bring a wealth of knowledge and expertise to every
            project. We offer custom solutions designed to fit your specific
            requirements, ensuring you get exactly what you need. Committed to
            delivering high-quality products and services, our rigorous quality
            assurance processes ensure that everything meets the highest
            standards. Your satisfaction is our priority, and we work closely
            with our clients to provide ongoing support and ensure their
            success. We stay ahead of the curve by continuously exploring and
            integrating the latest technologies and trends, ensuring our
            solutions are modern, efficient, and effective. Moreover, we offer
            competitive pricing without compromising on quality, providing
            maximum value for your investment. Choose Tekshu Innovations for
            your next project and experience the difference that expertise,
            quality, and customer-centric solutions can make.
          </p>
        </Box>
      </div>
      <PaperModel data={data1} />

      <div className="section1">
        <Box
          maxWidth="md"
          display="flex"
          flexDirection="column"
          justifyContent="center"
          alignItems="center"
          marginTop={5}
        >
          <h1>OUR PRODUCTS</h1>
          <p>
            At Tekshu Innovations, we offer a diverse range of cutting-edge
            products designed to elevate your digital presence and streamline
            your business operations. Our products include custom-built mobile
            apps, dynamic websites, robust APIs, and innovative software
            solutions tailored to meet your specific needs. With a focus on
            quality, usability, and scalability, our products are meticulously
            crafted to deliver exceptional performance and user experiences.
            Whether you're looking to enhance your online presence, streamline
            your workflows, or innovate your business processes, Tekshu
            Innovations has the perfect solution for you.
          </p>
        </Box>
      </div>
      <Container
        style={{
          paddingLeft: "200px",
          paddingRight: "200px",
          marginBottom: "50px",
        }}
      >
        <ProductCarousel
          products={products}
          productBuy={handleProductBuy}
          open={handleOpen}
        />
      </Container>
      <PaymentDialog open={open} handleClose={handleClose} product={product} />
    </div>
  );
}
