import React, { useState } from 'react';
import {
  Container, TextField, Button, Typography, Box, Grid, MenuItem, Select, InputLabel, FormControl, FormHelperText
} from '@mui/material';
import ReCAPTCHA from 'react-google-recaptcha';

export default function RegistrationForm({login}) {
  const [formData, setFormData] = useState({
    firstName: '',
    lastName: '',
    email: '',
    phone: '',
    skills: '',
    experience: '',
    github: '',
    linkedin: '',
    website: '',
    education: '',
    position: '',
    coverLetter: '',
    captchaValid: false,
  });

  const [error, setError] = useState('');

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleCaptchaChange = (value) => {
    setFormData({ ...formData, captchaValid: !!value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!formData.captchaValid) {
      setError('Please complete the CAPTCHA validation.');
      return;
    }
    // Submit form data
    console.log('Form submitted:', formData);
  };

  return (
    <Container maxWidth="md">
      <Box sx={{ mt: 8, display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
        <Typography component="h1" variant="h5">
          Developer Registration
        </Typography>
        {error && <Typography color="error" sx={{ mt: 2 }}>{error}</Typography>}
        <Box component="form" onSubmit={handleSubmit} sx={{ mt: 3 }}>
          <Grid container spacing={2}>
            <Grid item xs={12} sm={6}>
              <TextField
                required
                fullWidth
                id="firstName"
                label="First Name"
                name="firstName"
                value={formData.firstName}
                onChange={handleChange}
              />
            </Grid>
            <Grid item xs={12} sm={6}>
              <TextField
                required
                fullWidth
                id="lastName"
                label="Last Name"
                name="lastName"
                value={formData.lastName}
                onChange={handleChange}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                required
                fullWidth
                id="email"
                label="Email Address"
                name="email"
                value={formData.email}
                onChange={handleChange}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                required
                fullWidth
                id="phone"
                label="Phone Number"
                name="phone"
                value={formData.phone}
                onChange={handleChange}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                required
                fullWidth
                id="education"
                label="Education"
                name="education"
                value={formData.education}
                onChange={handleChange}
              />
            </Grid>
            <Grid item xs={12}>
              <FormControl fullWidth required>
                <InputLabel id="experience-label">Experience Level</InputLabel>
                <Select
                  labelId="experience-label"
                  id="experience"
                  name="experience"
                  value={formData.experience}
                  label="Experience Level"
                  onChange={handleChange}
                >
                  <MenuItem value="Junior">Junior</MenuItem>
                  <MenuItem value="Mid">Mid</MenuItem>
                  <MenuItem value="Senior">Senior</MenuItem>
                </Select>
                <FormHelperText>Select your experience level</FormHelperText>
              </FormControl>
            </Grid>
            <Grid item xs={12}>
              <TextField
                required
                fullWidth
                id="skills"
                label="Skills (comma-separated)"
                name="skills"
                value={formData.skills}
                onChange={handleChange}
              />
            </Grid>
            <Grid item xs={12}>
              <FormControl fullWidth required>
                <InputLabel id="position-label">Position Applied For</InputLabel>
                <Select
                  labelId="position-label"
                  id="position"
                  name="position"
                  value={formData.position}
                  label="Position Applied For"
                  onChange={handleChange}
                >
                  <MenuItem value="Frontend Developer">Frontend Developer</MenuItem>
                  <MenuItem value="Backend Developer">Backend Developer</MenuItem>
                  <MenuItem value="Full Stack Developer">Full Stack Developer</MenuItem>
                  <MenuItem value="DevOps Engineer">DevOps Engineer</MenuItem>
                  <MenuItem value="Data Scientist">Data Scientist</MenuItem>
                </Select>
                <FormHelperText>Select the position you are applying for</FormHelperText>
              </FormControl>
            </Grid>
            <Grid item xs={12}>
              <TextField
                fullWidth
                id="github"
                label="GitHub Profile"
                name="github"
                value={formData.github}
                onChange={handleChange}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                fullWidth
                id="linkedin"
                label="LinkedIn Profile"
                name="linkedin"
                value={formData.linkedin}
                onChange={handleChange}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                fullWidth
                id="website"
                label="Personal Website"
                name="website"
                value={formData.website}
                onChange={handleChange}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                fullWidth
                required
                multiline
                rows={4}
                id="coverLetter"
                label="Cover Letter"
                name="coverLetter"
                value={formData.coverLetter}
                onChange={handleChange}
              />
            </Grid>
            <Grid item xs={12}>
              <Box sx={{ mt: 2, display: 'flex', justifyContent: 'center' }}>
                <ReCAPTCHA
                  sitekey="6LdwheopAAAAAFmhBPHH7_dkrbJ-bg7e_CueGPZJ" // Replace with your actual reCAPTCHA site key
                  onChange={handleCaptchaChange}
                />
              </Box>
            </Grid>
          </Grid>
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            sx={{ mt: 3, mb: 2 }}
          >
            Register
          </Button>
          <Typography>Already have a developer account<Button variant='text' onClick={login}>Login</Button></Typography>
        </Box>
      </Box>
    </Container>
  );
}
